"use client"

import { useState } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { 
  Plus, 
  Truck, 
  Phone, 
  Users,
  Edit,
  Eye,
  EyeOff
} from "lucide-react"
import { Card, Button, Input, Badge, SectionLoader } from "@/components/ui"
import { formatPhone, generatePassword } from "@/lib/utils"
import toast from "react-hot-toast"

export default function DeliveryPersonsPage() {
  const queryClient = useQueryClient()
  const [showCreate, setShowCreate] = useState(false)
  const [editingId, setEditingId] = useState<string | null>(null)
  const [showPassword, setShowPassword] = useState(false)
  
  const [form, setForm] = useState({
    name: "",
    phone: "",
    zone: "",
    password: generatePassword(8),
  })

  const { data, isLoading } = useQuery({
    queryKey: ["delivery-persons"],
    queryFn: async () => {
      const res = await fetch("/api/admin/delivery-persons")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const createMutation = useMutation({
    mutationFn: async () => {
      const res = await fetch("/api/admin/delivery-persons", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(form),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: (data) => {
      toast.success("Delivery person created")
      queryClient.invalidateQueries({ queryKey: ["delivery-persons"] })
      setShowCreate(false)
      resetForm()
      
      // Show credentials
      toast.custom((t) => (
        <div className="bg-white p-4 rounded-xl shadow-lg border max-w-sm">
          <p className="font-semibold mb-2">Credentials Created</p>
          <p className="text-sm text-neutral-600">Phone: {form.phone}</p>
          <p className="text-sm text-neutral-600">Password: {form.password}</p>
          <p className="text-xs text-amber-600 mt-2">Save these credentials - password won't be shown again!</p>
        </div>
      ), { duration: 10000 })
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const updateMutation = useMutation({
    mutationFn: async (data: { id: string; updates: any }) => {
      const res = await fetch(`/api/admin/delivery-persons/${data.id}`, {
        method: "PATCH",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data.updates),
      })
      if (!res.ok) {
        const result = await res.json()
        throw new Error(result.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Updated successfully")
      queryClient.invalidateQueries({ queryKey: ["delivery-persons"] })
      setEditingId(null)
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const resetForm = () => {
    setForm({
      name: "",
      phone: "",
      zone: "",
      password: generatePassword(8),
    })
  }

  const deliveryPersons = data?.data?.deliveryPersons || []

  return (
    <div className="p-6">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-2xl font-bold text-neutral-900">Delivery Team</h1>
          <p className="text-neutral-500">Manage delivery person accounts</p>
        </div>
        <Button onClick={() => setShowCreate(true)}>
          <Plus className="h-4 w-4" />
          Add Person
        </Button>
      </div>

      {/* Create Form */}
      {showCreate && (
        <Card className="mb-6">
          <h3 className="font-semibold text-neutral-900 mb-4">Create Delivery Person</h3>
          <div className="grid md:grid-cols-2 gap-4">
            <Input
              label="Full Name"
              value={form.name}
              onChange={(e) => setForm({ ...form, name: e.target.value })}
              placeholder="Enter full name"
            />
            <Input
              label="Phone Number"
              value={form.phone}
              onChange={(e) => setForm({ ...form, phone: e.target.value })}
              placeholder="10-digit phone number"
              hint="This will be used as username"
            />
            <Input
              label="Zone/Area"
              value={form.zone}
              onChange={(e) => setForm({ ...form, zone: e.target.value })}
              placeholder="e.g., Pondicherry Central"
            />
            <div>
              <Input
                label="Password"
                type={showPassword ? "text" : "password"}
                value={form.password}
                onChange={(e) => setForm({ ...form, password: e.target.value })}
                rightIcon={
                  <button onClick={() => setShowPassword(!showPassword)}>
                    {showPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
                  </button>
                }
              />
              <button
                type="button"
                onClick={() => setForm({ ...form, password: generatePassword(8) })}
                className="text-xs text-primary-600 mt-1"
              >
                Generate new password
              </button>
            </div>
          </div>
          <div className="flex gap-3 mt-4">
            <Button
              onClick={() => createMutation.mutate()}
              loading={createMutation.isPending}
              disabled={!form.name || !form.phone || !form.password}
            >
              Create
            </Button>
            <Button variant="ghost" onClick={() => { setShowCreate(false); resetForm(); }}>
              Cancel
            </Button>
          </div>
        </Card>
      )}

      {/* List */}
      {isLoading ? (
        <SectionLoader />
      ) : deliveryPersons.length === 0 ? (
        <Card className="text-center py-12">
          <Truck className="h-12 w-12 text-neutral-300 mx-auto mb-4" />
          <p className="text-neutral-500">No delivery persons yet</p>
          <Button className="mt-4" onClick={() => setShowCreate(true)}>
            Add First Person
          </Button>
        </Card>
      ) : (
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-4">
          {deliveryPersons.map((person: any) => (
            <Card key={person.id}>
              <div className="flex items-start justify-between mb-4">
                <div className="flex items-center gap-3">
                  <div className="h-12 w-12 bg-primary-100 rounded-xl flex items-center justify-center">
                    <Truck className="h-6 w-6 text-primary-600" />
                  </div>
                  <div>
                    <p className="font-semibold text-neutral-900">{person.name}</p>
                    <p className="text-sm text-neutral-500">{person.zone || "No zone assigned"}</p>
                  </div>
                </div>
                <Badge variant={person.isActive ? "success" : "error"} size="sm">
                  {person.isActive ? "Active" : "Inactive"}
                </Badge>
              </div>

              <div className="space-y-2 mb-4">
                <div className="flex items-center gap-2 text-sm">
                  <Phone className="h-4 w-4 text-neutral-400" />
                  <span>{formatPhone(person.phone)}</span>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <Users className="h-4 w-4 text-neutral-400" />
                  <span>{person._count?.customers || 0} customers assigned</span>
                </div>
              </div>

              <div className="flex gap-2">
                <Button
                  variant="outline"
                  size="sm"
                  className="flex-1"
                  onClick={() => {
                    // Toggle active status
                    updateMutation.mutate({
                      id: person.id,
                      updates: { isActive: !person.isActive }
                    })
                  }}
                >
                  {person.isActive ? "Deactivate" : "Activate"}
                </Button>
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => setEditingId(person.id)}
                >
                  <Edit className="h-4 w-4" />
                </Button>
              </div>

              {/* Edit Form */}
              {editingId === person.id && (
                <div className="mt-4 pt-4 border-t border-neutral-200 space-y-3">
                  <Input
                    label="Name"
                    defaultValue={person.name}
                    id={`name-${person.id}`}
                  />
                  <Input
                    label="Zone"
                    defaultValue={person.zone}
                    id={`zone-${person.id}`}
                  />
                  <Input
                    label="New Password (optional)"
                    type="password"
                    placeholder="Leave blank to keep current"
                    id={`password-${person.id}`}
                  />
                  <div className="flex gap-2">
                    <Button
                      size="sm"
                      onClick={() => {
                        const name = (document.getElementById(`name-${person.id}`) as HTMLInputElement).value
                        const zone = (document.getElementById(`zone-${person.id}`) as HTMLInputElement).value
                        const password = (document.getElementById(`password-${person.id}`) as HTMLInputElement).value
                        
                        updateMutation.mutate({
                          id: person.id,
                          updates: {
                            name,
                            zone,
                            ...(password && { password })
                          }
                        })
                      }}
                      loading={updateMutation.isPending}
                    >
                      Save
                    </Button>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => setEditingId(null)}
                    >
                      Cancel
                    </Button>
                  </div>
                </div>
              )}
            </Card>
          ))}
        </div>
      )}
    </div>
  )
}
